/// A SASS stylesheet for Icarus, extending Bootstrap 3.3.7
/// @author Ryan Dunphy
/// @see SASS Variables: https://sass-lang.com/guide#topic-2-SCSS
/// @see http://sassdoc.com/annotations/
/// @see https://webdevstudios.com/2015/03/17/getting-started-with-sassdoc/
/// @see Consider http://sass-lang.com/documentation/Sass/Script/Functions.html#saturate-instance_method
/// @todo Implement SASSDoc
$color-white: #fff;
$color-black: #000;
$color-blue: rgb(0, 0, 255);
$color-blue-light: #bee1ef;
$color-blue-lighter: #00dcff;
$color-blue-deep: #005879;
$color-orange: rgb(255, 165, 0);
$color-red: rgb(255, 0, 0);
$color-red-deep: #641818;
$color-primary: #555;
$color-secondary-neutral: #4a6879;
$color-secondary-dark: #353e52;
$color-statement-even: #98a594;
$color-statement-odd: #6e7880;
$color-scrollbar-face: #999;
$color-grey: #666;
$color-grey-dark: darken($color-grey, 25%);
$color-grey-darker: darken($color-grey, 50%);
$color-grey-light: lighten($color-grey, 25%);
$color-grey-lighter: lighten($color-grey, 50%);
$color-shadow-light: transparentize($color-black, .8);
$color-shadow-neutral: transparentize($color-black, .5);
$color-shadow-dark: transparentize($color-black, .2);
$color-light-less: #efefef;
$color-light: #eee;
$color-light-more: #fafafa;
$color-nav-background: #6e7880;
$color-darker: rgb(16, 16, 16);
$color-dark: rgb(23, 23, 23);
$color-dark-less: rgb(33, 33, 33);
$content-maxwidth: 900px;
$container-bgcolor: adjust-color($color-black, $alpha: .8);
$font-header: 'Raleway', sans-serif;
$font-major: 'Raleway', sans-serif;
$font-minor: 'Lato', sans-serif;
$font-primary: 'Lato', sans-serif;
$font-primary-weight: 300;
$font-size-small: .5em;
$font-size-normal: 1em;
$font-size-large: 1.5em;
$font-size-extralarge: 2.5em;
$navbar-height: 4em; // 50px
$sidebar-width: 80%;
$max-width: 768px;
$thumbnail-height: 10em;
$transition-speed: .2s;
$transition-motion: ease-in-out;
$transition-default: all $transition-speed $transition-motion;
$icon-width: 4em; //64px;
$icon-height: 4em; //64px;
$jumbotron-bg-default: #008196;

// Mixins: https://sass-lang.com/guide#topic-6-SCSS
@mixin transition($property) {
	transition: $property;
}
@mixin boxShadow($x, $y, $blur, $spread, $color) {
	box-shadow: $x $y $blur $spread $color;
}
@mixin boxShadowInset($x, $y, $blur, $spread, $color) {
	box-shadow: inset $x $y $blur $spread $color;
}
/// A Webkit scrollbar
/// @param {size} $size
@mixin custom-scroll-bar($width, $height, $foreground-color, $background-color: mix($foreground-color, $color-white, 50%)) {
	// For Google Chrome
	::-webkit-scrollbar {
		border-radius: 10px;
		height: $height;
		width: $width;
	}

	::-webkit-scrollbar-thumb {
		background: $foreground-color;
		border-radius: 10px;
	}

	::-webkit-scrollbar-track {
		background: $background-color;
		border-radius: 10px;
	}
	// For Internet Explorer
	body {
		scrollbar-face-color: $color-scrollbar-face;
		scrollbar-track-color: $background-color;
	}
}
/// no scroll bar
@mixin no-scroll-bar {
	&::-webkit-scrollbar {
		height: 0;
		width: 0;
	}
	&::-webkit-scrollbar-thumb {
		height: 0;
		width: 0;
	}
	&::-webkit-scrollbar-track {
		height: 0;
		width: 0;
	}
}
@mixin font-smoothing($value: antialiased) {
	@if ($value==antialiased) {
		font-smoothing: antialiased;
	}
	@else {
		font-smoothing: auto;
	}
}
// Abstract Classes: Inheritance: https://sass-lang.com/guide#topic-7-SCSS
/// Prevents the user from selecting this element
%noSelect {
	cursor: default;
	user-select: none;
}
%horizontal-scroll {
	// mobile only
	//@media only screen and (max-width: 767px) {
	display: flex;
	flex-wrap: nowrap;
	overflow-x: auto;
	overflow-y: hidden;
	//}
	//@media only screen and (max-width: 768px) {    }
}
/// The object can be clicked or tapped
%clickable {
	cursor: pointer;
	&:hover {
		text-decoration: underline;
	}
}
%gradient-primary {
	$color-one: $color-blue-lighter;
	$color-two: $color-blue-deep;
	background-image: radial-gradient(ellipse farthest-corner at left center, $color-one 0%, $color-two 100%);
}
%gradient-vignette {
	background: radial-gradient(ellipse at center,
		$color-shadow-light 0%,
		$color-shadow-neutral 60%,
		$color-shadow-dark 100%);
}
%gradient-vertical-fade {
	background: linear-gradient(to bottom,
		$color-shadow-dark 0%,
		$color-shadow-light 100%);
}
%horizontal-center {
    clear: both;
    float: none;
    margin-left: auto;
    margin-right: auto;
    max-width: $max-width;
}
/// A commonly used bevel to offset rows
%bevel {
	border-left: 0;
	border-right: 0;
	//clear: both;
	//float: left;
	line-height: $navbar-height;
	min-height: $navbar-height;
	//width: 100%;
}
/// The bevel appears to be coming towards the screen
%bevel-up {
    @extend %bevel;
    //border-bottom: 1px solid $color-darker;
    //border-top: 1px solid $color-grey-darker;
    border-bottom: 2px solid transparentize($color-grey-dark, .8);
    border-top: 1px solid transparentize($color-grey-light, .8);
}
/// The bevel appears to be going away from the screen
%bevel-down {
    @extend %bevel;
    //border-bottom: 1px solid $color-grey-darker;
    //border-top: 1px solid $color-darker;
    border-bottom: 1px solid transparentize($color-grey-light, .8);
    border-top: 2px solid transparentize($color-grey-dark, .8);
}
/// A content window that is the height of the page, minus header and footer
%pane-tall {
	$navheights: ($navbar-height * 2) - 1em;
	height: calc(100vh - #{$navheights}); // Interpolate for calc https://stackoverflow.com/a/20236515/722785
	overflow-y: auto; // !important
	padding-bottom: 2em;
}

%thumbnail-image {
    border: 1px solid $color-shadow-neutral;
    border-radius: .2em;
    box-shadow: 0 2px 1px 1px $color-shadow-light;
    display: block;
}
// Generic UI tweaks
.noSelect {
	@extend %noSelect;
}
.clickable {
	@extend %clickable;
}
.edit {
	border: dashed 1px $color-white;
}
.dropdown-tab {
	width: 160px;
	>a {
		display: block;
		float: right;
	}
	>.dropdown-toggle {
		background-color: $color-grey-darker;
		border-right: 1px solid $color-grey;
	}
}

body {
	@include font-smoothing;
	background-color: $color-red; //$color-dark-less; //$background-primary;
	color: $color-primary;
	font-family: $font-primary;
	font-size: 14px; //$font-size-normal;
	font-weight: $font-primary-weight;
	overflow: hidden;
	padding-top: $navbar-height;
}
button {
	:disabled {
		background-color: $color-red-deep;
	}

	>.icon {
		color: $color-black;
	}

	.glyphicon {
		.icon-label {
			color: $color-black;
			text-transform: uppercase;
		}
	}
}
div {
	.btn-group {
		.btn {
			&:hover {
				background-color: $color-blue-light;
			}
		}
	}
}
.btn {
	&.glyphicon {
		.icon-label {
			color: $color-black;
			font-family: sans-serif;
			padding: 0 0 0 4px;
			text-transform: uppercase;
			top: -3px;
		}
	}
}
.btn-block {
	border-bottom: 2px solid $color-grey-dark;
	border-left: 0;
	border-radius: 0;
	border-right: 0;
	border-top: 1px solid $color-blue-light;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	color: $color-dark;
	font-family: $font-major;
	font-weight: $font-primary-weight;
}
h2 {
	input {
		&[type='number'] {
			display: block;
			float: right;
			text-align: right;
			width: 70px;
		}
	}
}
dialog {
	border: 0;
	border-radius: .6rem;
	box-shadow: 0 0 1em $color-black;
	margin: 1em;
	padding: 0;

	&::backdrop {
		background-color: $color-shadow-dark;
	}
}
nav {
	@extend %bevel-up;
	background-color: $color-nav-background;
	font-size: $font-size-normal;
	height: $navbar-height;

	.btn-group {
		>.dropdown-menu {
			>li {
				color: $color-grey;
				text-align: left;
				text-decoration: none;
			}
		}
	}
}
/// A collapseable menu
.menu {
    background-color: $color-nav-background;

    > header {
        @extend %bevel-up;
        @extend %clickable;
        background-color: darken($color-nav-background, 25%);
        color: $color-light;
        padding: 0 .2em;
        text-transform: uppercase;

        &:hover {
            background-color: lighten($color-nav-background, 25%);
        }
        // First ul after nav header
        & ~ ul {
            .nav {
                background-color: darken($color-nav-background, 50%);
            }
        }
    }

    .list {
        margin: 0;
        padding: 0;
        width: 100%;

        &.tabs {
            .wide-tab {
                text-align: center;
                width: calc(100% - 8em);
            }
        }

        &.horizontal {
            @extend %horizontal-scroll;
            //@media only screen and (max-width: 767px) {}
            //@media only screen and (max-width: 768px) {}
            @include no-scroll-bar;

            &.collapse {
                height: 0;

                &.in {
                    height: 4em; //96px; //$navbar-height;
                }
            }

            > .nav-item-icon {
                display: inline-block;

                > a {
                    display: block;
                    width: 8em;

                    span {
                        float: left;
                        line-height: inherit;


                        &.text {
                            overflow: hidden;
                            padding: 0 .3em;
                            text-overflow: ellipsis;
                            white-space: nowrap;
                            width: 5em;
                        }
                    }
                }
            }
        }

        .icon-label {
            @extend %noSelect;
        }

        .nav-item {
            @extend %clickable;
            background-color: $color-shadow-neutral;
            border: thin solid $color-grey-darker;
            border-radius: .4em;
            cursor: pointer;
            float: left;
            line-height: $navbar-height - 1em;
            list-style: none;
            margin: 4px;
            //padding: 0;
            //text-align: center;
            &:hover {
                background-color: $color-shadow-light;
            }

            > a {
                color: $color-light;
                //line-height: 1em;
                padding: 0 1em;

                &:hover {
                    color: $color-light-more;
                }
            }

            &.nav-item-icon {
                > a {
                    > .icon {
                        > .icon-label {
                            //clear: both;
                            display: block;
                            //float: none;
                            font-size: $font-size-small;
                            line-height: 1.5em;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            text-transform: uppercase;
                            white-space: nowrap;
                            width: 5em; //$icon-width;
                        }
                    }
                }
            }
        }
    }
}


.icarus-container {
	margin: 0;
	padding: 0;
	width: 100%;

	.panel {
		background-color: adjust-color($color-black, $alpha: .5);
	}

	>.container-body {
		background-color: $color-light;
		width: 100%;

		.pane {
			@include transition($transition-default);
			@include no-scroll-bar;
			width: 100%;
		}
	}
}

.index-main {
    > .container-body {
        background-color: darken($color-grey-dark, 5%);

        > header {
            @extend %bevel-up;
            background-color: $color-dark;
            color: $color-light;
            padding: 0 .5em;
            text-transform: uppercase;
        }

        > .banner {
            @extend %horizontal-center;
            border-bottom: 0;
            border-left: 2px solid $color-darker;
            border-right: 2px solid $color-darker;
            border-top: 0;
            height: 30em;
            //margin: 0 auto;
            max-height: 528px;
            //max-width: $max-width;
            overflow-y: auto;
        }
    }

    .pagination {
        //footer.pagination
        border-radius: 0;
        margin: 0;
        padding: .5em;
        text-align: center;
        width: 100%;
    }
}
main {
	align-items: stretch;
	display: flex;

	// The pane within the Main container should fill the remaining screen space
	>.container-body {
		>.pane {
			@extend %pane-tall;
		}
	}
	// A left aligned Container (sidebar) for the MAIN UI
	aside {
		&.sidebar {
			@extend %pane-tall;
			@include boxShadow(0, 0, 3px, 0, $color-black);
			@include no-scroll-bar;
			@include transition($transition-default);
			background-color: transparentize($color-black, .2);
			margin-left: -100%; /// !important
			position: absolute;
			top: $navbar-height;
			width: $sidebar-width; // !important
			z-index: 999;

			&.active {
				margin-left: 0;
			}
			// Each embedded list gets smaller
			ul {
				li {
					ul {
						@include boxShadowInset(0, 1px, 1px, 0, rgba(1, 1, 1, .2));
						font-size: 90%;
						text-indent: 1em;
					}
				}
			}
		}
	}
}
.banner {
	@extend %bevel-up;
	@extend %noSelect;
	background-color: $color-light-more;
	//padding: 1.4em 0;
}
.callout {
	padding: 0;
	text-align: center;
	&>.icon {
		font-size: 3em;
	}
}



.chat {
    background-color: $color-light-more;

    & .pane {
        height: 400px;
        max-height: 400px;
        overflow-y: auto;
        width: 100%;
    }

    & .statement {
        background-color: $color-orange;
        border-bottom: 2px solid transparentize($color-grey-dark, .8);
        border-top: 1px solid transparentize($color-grey-light, .8);
        float: left;
        padding: .5em 0;
        width: 100%;

        &:nth-child(odd) {
            background-color: $color-statement-odd;
        }

        &:nth-child(even) {
            background-color: $color-statement-even;
        }
    }

    .thumb {
        float: left;
        margin-right: 1em;

        & .user-photo {
            border: 1px solid $color-grey-darker;
            border-radius: 64px;
            height: 64px;
            margin: 4px 0 0 12px;
            width: 64px;
        }
    }

    .bubble {
        float: left;
        width: calc(100% - 124px);
    }

    .panel {
        margin-top: 20px;
        position: relative;

        .panel-body {
            background-color: $color-white;
        }

        .panel-heading {
            cite {
                display: block;
                font-size: $font-size-small;
            }

            &::after,
            &::before {
                border-color: transparent;
                border-style: solid solid outset;
                content: ' ';
                display: block;
                height: 0;
                left: -16px;
                pointer-events: none;
                position: absolute;
                right: 100%;
                top: 11px;
                width: 0;
            }

            &::after {
                border-right-color: $color-light-more;
                border-width: 7px;
                margin-left: 2px;
                margin-top: 1px;
            }

            &::before {
                border-right-color: $color-light-more;
                border-width: 8px;
            }
        }
    }

    footer {
        padding: 1em;
        position: relative;

        textearea {
            border: 2px solid $color-blue-light;
            border-radius: 6px;
            color: $color-white;
            font-size: 18px;
            font-weight: bold;
            padding: .2em .5em;
        }
    }

    .nav-pills {
        > li {
            display: block;
            float: left;
            list-style: none;
        }
    }

    .word-summary {
        > li {
            > a {
                color: $color-blue;
            }
        }
    }
}

.console {
	@include transition($transition-default);
	background-color: adjust-color($color-black, $alpha: .75);
	color: $color-grey-light;
	display: block;
	font-family: monospace;
	font-size: 8pt;
	line-height: 2em;
	list-style: none;
	margin: 0;
	max-height: 200px;
	overflow-y: auto;
	padding: 1em;
}
footer {
    @extend %bevel-up;
    @include boxShadow(0, -1px, 2px, 0, $color-shadow-dark);
    background-color: $color-grey-dark;
    //border-top: 1px solid $color-grey-light;
    clear: both;
    color: $color-white;
    display: block;
    float: left;
    font-size: smaller;
    height: $navbar-height;
    line-height: $navbar-height - 1em;
    padding: .5em;
    width: 100%;
    height: $navbar-height;

    > .container {
        margin: .4em auto;
    }

    > cite {
        float: right;
        line-height: 16px;
        text-align: right;

        a {
            color: $color-white;
            text-decoration: underline;

            &:hover {
                color: $color-blue-lighter;
            }
        }
    }

    &.stickyfooter {
        @extend %bevel-up;
        @include transition($transition-default);
        bottom: 0;
        padding: 0;
        position: fixed;

        &.active {
            height: $navbar-height * 5;
        }

        textarea {
            border-radius: 5px;
            color: $color-black;
            height: 180px;
            line-height: normal;
            margin: 1em;
            width: calc(100% - 25px);
        }
    }
}
form {
	@extend %bevel-up;
	.login {
		input,
		label {
			text-align: center;
		}
	}
	footer {
		&.form-footer {
			background-color: transparent;
			padding: 0;
		}
	}
}
input,
select,
textarea {
	margin: 0;
	width: 100%;
}
input {
	&[type='submit'] {
		background-color: $color-blue-deep;
		color: $color-white;
		font-size: larger;
		font-weight: bold;
		height: 50px;
		text-transform: uppercase;
		width: 100%;
		&:hover {
			background-color: $color-blue-lighter;
		}
		&:disabled {
			background-color: $color-red-deep;
		}
	}
}
.menulist-menu {
	border-bottom: 3px solid $color-grey-darker;
	border-top: 1px solid $color-grey;
	max-height: 200px;
	overflow-y: auto;
}
.index-thumbnail-menulist {
	max-height: 200px;
	overflow-y: auto;
}
// Nav Item within a MenuList
.menulist {
	max-height: 200px;
	overflow-y: auto;
	& .container-body {
		& ul {
			&.nav {
				& .nav-item {
					@extend %bevel-up;
					background-color: $color-grey-darker;
					color: $color-light-more;
					line-height: 3em;
					padding: 0 1em;
					&:hover {
						background-color: $color-grey-dark;
						color: $color-light-more;
					}
				}
			}
		}
	}
}
.toggle {
	>.toggle-wide {
		@extend %bevel-up;
		background-color: $color-dark;
		color: $color-light;
		font-size: 8px;
		font-weight: bolder;
		height: 12px;
		line-height: 12px;
		padding: 0;
		text-align: center;
		text-transform: uppercase;
	}
}
.navbar-search {
	border: 0;
	border-bottom: 1px solid $color-white;
	padding: .3em;
}
.glyphicon {
	.icon-label {
		color: $color-grey-light;
		font-family: Lato;
	}
}

// Override the default bootstrap behavior where horizontal description lists 
// will truncate terms that are too long to fit in the left column
.dl-horizontal {
	dt {
		white-space: normal;
	}
}
.badge-group {
	border: thin solid $color-white;
	border-radius: .3em;
	height: 240px;
	margin-bottom: 1em;
	overflow-y: auto;
	padding: 1em;
}
.form-group {
	margin-bottom: 0;
}
.list-group {
	.list-group-item {
		:hover {
			background-color: $color-light-more;
		}
	}
}
.control-label {
	&:hover {
		color: $color-blue-deep;
		cursor: pointer;
		text-decoration: underline;
	}
}
.dropdown-menu {
	ul {
		&.menu {
			border-bottom: 1px solid $color-white;
		}
	}

	&.navbar-inverse {
		.nav-item {
			a {
				color: $color-light;

				.nav-item {
					a {
						&:hover {
							color: $color-light-more;
							cursor: pointer;
							text-decoration: underline;
						}
					}
				}
			}
		}
	}
}
.jumbotron {
	@extend %noSelect;
	//@extend %bevel-down;
	@extend %gradient-vignette;
	background-color: $jumbotron-bg-default;
	background-position: center top;
	background-repeat: no-repeat;
	background-size: cover;
	color: $color-white;
	font-family: 'Raleway';
	font-weight: 300;
    line-height: 2em;
	margin: 0;
	min-height: 420px;
	overflow-y: hidden;
	padding: 0;
	position: relative;
	text-align: center;
	//text-shadow: 1px 1px 4px $color-shadow-dark;
	width: 100%;

	>.screen {
		@extend %gradient-vignette;
		height: 100%;
		position: absolute;
		width: 100%;
	}

	header {
		font-size: $font-size-extralarge;
        line-height: 3em;
		padding: 0 6px;
	}

	p {
		@extend %horizontal-center;
        font-weight: normal;
		padding: 0 2em;        
	}
}
// IMAGE GALLERY A browseable list of images
.image-gallery {
	color: inherit;

	>.container-body {
		&>header {
			color: $color-light;
			font-family: sans-serif;
			font-size: $font-size-small;
			font-weight: bold;
			padding: .2em 1em;
			text-align: right;
			text-transform: uppercase;
		}

		&>.banner {
			height: 500px;
			max-height: 500px;
			overflow-y: auto;
		}
	}

	.thumbnail {
		&>img {
            @extend %thumbnail-image;			
			clear: both;
			float: none;
			max-height: 256px;
			max-width: 256px;
		}
	}

	footer {
		&.pagination {
			border-radius: 0;
			margin: 0;
			text-align: center;
			width: 100%;
		}
	}
}
// LABEL
label {
	clear: both;
	color: $color-grey-darker;
	display: block;
	float: left;
	font-size: smaller;
	margin: .3em 0;
	text-align: left;
	text-transform: uppercase;
	width: 100%;
	&.invalid {
		color: $color-red;
	}
}
// MODAL
/// Header located at the top of the modal
/// Contains a label and a close button
.modal-header {
	@include boxShadow(0, 1px, 1px, 0, rgba(1, 1, 1, .3));
	background-color: $color-grey-darker;
	border-bottom: 1px solid $color-grey;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	color: $color-white;
	font-weight: bolder;
	padding: .5em 1em;

	.close {
		color: $color-blue-lighter;
	}
}
.modal-body {
	max-height: 85vh;
	overflow-y: auto;
	padding: 0;
}
.modal-footer {
	padding: 0;

	.btn {
		@include boxShadowInset(0, 2px, 2px, 0, $color-shadow-light);
		background-color: $color-grey-light;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
		border-color: $color-grey-light;
		border-top: 1px solid $color-grey;
		color: $color-grey-darker;
		cursor: pointer;
		font-weight: bold;
		text-shadow: 0 -1px 1px $color-grey;
		text-transform: uppercase;

		&:hover {
			@include transition(color 400ms);
			@include transition(background-color 400ms);
			background-color: $color-blue-light;
			color: $color-secondary-neutral;
		}
	}
}
// https://stackoverflow.com/questions/18053408/vertically-centering-bootstrap-modal-window
.vertical-alignment-helper {
	display: table;
	height: 100%;
	pointer-events: none; // This makes sure that we can still click outside of the modal to close it
	width: 100%;
}
.vertical-align-center {
	display: table-cell;
	pointer-events: none;
	vertical-align: middle;
	.modal-content {
		margin: 0 auto;
	}
}
.modal-content {
	margin: 0 auto;
	max-width: inherit;
	pointer-events: all;
	width: inherit;
	.well {
		margin-bottom: 0;
	}
}
// LOADER
.modal-loader {
	z-index: 99999;

	.container-body {
		background-color: adjust-color($color-black, $alpha: .1);
	}

	.modal-dialog {
		width: 80%;
	}
}
.modal {
	.fade {
		.modal-dialog {
			margin: 0;
			padding: .3em;
			transform: translate(0);
			width: 100%;
		}
	}
	.loader {
		height: 260px;
		margin-left: 1em;
		margin-right: 1em;
		max-height: 260px;
		.progress {
			margin: .5em;
			text-decoration: none;
			text-transform: uppercase;
			width: calc(100% - 1em);
			.progress-bar {
				text-shadow: 0 1px 4px $color-shadow-neutral;
			}
		}
		.console {
			@include no-scroll-bar;
			margin-left: .5em;
			margin-right: .5em;
			&.collapse {
				border: 0;
				border-radius: 5px;
				height: 0;
				margin-top: 0;
				opacity: 0;
				padding: 0;
				&.in {
					height: auto;
					opacity: 1;
					padding: initial;
				}
			}
		}
		.modal-body {
			height: auto;
			.well {
				font-size: smaller;
				margin: 0;
			}
		}
		.modal-footer,
		.modal-header {
			display: none;
		}
		.modal-content {
			background-color: $color-shadow-neutral;
		}
	}
	.prompt {
		.modal-body {
			height: auto;
			.well {
				font-size: smaller;
				margin: .3em 1em;
				max-height: 200px;
				overflow: auto;
			}
		}
		.modal-dialog {
			padding: .3em;
		}
		.modal-footer {
			display: none;
		}
	}
}
.text-center {
	text-align: center;
}
.input-image-preview {
	background-color: $color-white;
	border: 4px solid $color-white;
	border-radius: .3em;
	height: auto;
	margin: 1em;
	max-width: 120px;
	min-height: 64px;
	min-width: 64px;
	padding: .3em;
}
// PREVIEW
.preview {
	background-color: $color-light-more;
	border-bottom: 3px solid $color-grey-darker;
	border-top: 3px solid $color-secondary-dark;
	height: 400px;
	margin: 0;
	max-height: 400px;
	overflow-y: auto;
}
.preview-notes {
	@extend %bevel-up;
	background-color: $color-grey-light;
	color: $color-grey-darker;
	font-size: small;
	font-weight: bold;
	height: 2em;
	line-height: 2em;
	margin: 0;
	overflow: hidden;
	padding: 0 1em;
}
.index {
	background-color: $color-shadow-neutral;
	color: inherit;

	.container-body {
		>.banner {
			>header {
				background-color: $color-black;
				border-bottom: 1px solid $color-grey;
				color: $color-grey-light;
				padding: 0 .5em;
			}
		}

		>.pane {
			@extend %bevel-up;
			background-color: $color-grey-darker;
			padding: 0;
		}
	}
}
// SECTION
/// Due to implementation of dropdown-group
.dropdown-menu,
.dropdown-group {
	li {
		>a {
			clear: both;
			color: $color-white;
			display: block;
			font-weight: 400;
			line-height: 1.42857143;
			padding: 3px 20px;
			white-space: nowrap;
		}
	}
}
.form-element {
	.container-body {
		.pane {
			padding: .4em;
		}
	}
}
/// Fixed TBody height
.table-fixed {
	font-size: smaller;
	table {
		font-size: smaller;
	}
	thead {
		border-bottom: 2px solid $color-white;
		padding-right: 18px;
	}
	th {
		background-color: $color-light-more;
		border: 0;
		font-size: smaller;
		text-transform: uppercase;
	}
	tr {
		border-bottom: 1px solid $color-light;
		border-top: 1px solid $color-grey;
		display: inline-table;
		table-layout: fixed;
		width: 100%;
		&:hover {
			background-color: $color-light-more;
		}
	}
	td,
	th {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	tbody {
		border-bottom: thin solid $color-white;
	}
	.btn-group-sm {
		.btn {
			height: 24px;
			line-height: 12px;
		}
	}
}
// TEXTBLOCK
.textblock {
	font-family: 'Raleway';
	//background-color: red;
	h1 {
		border-bottom: 1px solid $color-grey;
		margin: 4px 0 8px;
		padding: 4px;
	}
	p {
		line-height: 1.8em;
		margin: .5em auto 2em;
		max-width: $content-maxwidth;
		padding: 0 1.8em;
		text-align: justify;
	}
	.paragraph {
		header {
			@extend %bevel-down;
			@extend %gradient-vertical-fade;
			color: $color-light;
			height: $navbar-height;
			line-height: $navbar-height;
			padding: 0 1em;
		}
	}
}
.thumbnail {
    @extend %bevel-up;
    @extend %clickable;
    background-color: $color-grey-dark;
    border-radius: 0;
    //@media only screen and (max-width: 767px) {}
    //@media only screen and (max-width: 768px) {}
    font-family: 'Raleway';
    height: $thumbnail-height;
    margin: 0;
    padding: 0;

    &.nav-item {
        a {
            height: $thumbnail-height;
            line-height: 1.5em;
            width: 100%;

            img {
                @extend %thumbnail-image;
                clear: left;
                float: left;
                height: $thumbnail-height - 1em;
                margin: .5em;
                width: $thumbnail-height - 1em;
            }
        }
    }

    .container-body {
        .pane {
            background-color: $color-light;
            color: $color-dark;

            > header {
                clear: right;
                color: $color-light-less;
                display: block;
                float: left;
                font-weight: bold;
                line-height: 3em;
                margin: .5em 0 0;
                padding: 0;
                text-transform: uppercase;
                width: calc(100% - 148px);
            }

            > img {
                background-color: $color-white;
                border: 1px solid $color-white;
                border-radius: .3em;
                clear: left;
                display: block;
                float: left;
                height: 128px;
                margin: .3em;
                width: 128px;
                //border-radius: .3em .3em 0 0;
            }

            > p {
                color: $color-light-less;
                float: left;
                font-size: smaller;
                height: 6em;
                margin: 0 .3em;
                overflow-y: auto;
                padding: 0 .8em;
                text-align: justify;
                width: calc(100% - 148px);
            }

            > .btn-group {
                bottom: 1.75em;
                position: absolute;
                right: 1.35em;

                > button {
                    &.btn {
                        border: 0;
                        border-radius: .3em;
                    }
                }
            }
        }
    }
}
.form-element-group {
	border-bottom: thin solid $color-grey-light;
	position: relative;
	transition: position 800ms;
	transition-delay: 300ms;
}
.index-thumbnail {
	margin-bottom: 0;
}
